$star-color: red;
$duration: 100s;
$count: 500;

@function getShadows($n) {
  $shadows: '#{random(100)}vw #{random(100)}vh 5px #{$star-color}';

  @for $i from 2 through $n {
    $shadows: '#{$shadows}, #{random(100)}vw #{random(100)}vh 3px #{$star-color}';
  }

  @return unquote($shadows);
}

@for $i from 1 through 10 {
  $duration: floor($duration / 2);
  $count: floor($count / 2);

  .layer#{$i} {
    $size: #{$i * 2}px;
    position: fixed;
    width: $size;
    height: $size;
    border-radius: 50%;
    left: 0;
    top: 0;
    box-shadow: getShadows($count);
    animation: moveUp $duration linear infinite;

    &::after {
      content: '';
      position: fixed;
      left: 0;
      top: 100vh;
      width: $size;
      height: $size;
      border-radius: inherit;
      box-shadow: inherit;

    }
  }
}

@keyframes moveUp {
  100% {
    transform: translateY(-100vh);
  }
}