$bg: #ffffff;
$shadow-color: #6d6d6d;

.radio-input {
  position: sticky;
  top: 0;
  margin-left: 10% !important;
  margin-right: 10% !important;
  display: flex;
  align-items: center;
  border-radius: 999px;
  background-color: #{$bg};
  color: #000000;
  overflow: hidden;
  border: 2px solid #{$bg};
  font-weight: 500;
  letter-spacing: -1px;
  font-size: 14px;
  box-shadow: 4px 4px #{$shadow-color};

  input {
    display: none;
  }

  label {
    width: calc(100% / var(--count));
    padding: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;

    &:has(input:checked) {
      color: #185ee0;

      ~ .selection {
        background-color: #e6eef9;
        display: inline-block;
      }
    }

    &:hover {
      color: #e09a18;
      transform: scale(1.05);
      transition: .1s ease;
    }
  }

  .selection {
    display: none;
    position: absolute;
    height: 100%;
    width: calc(100% / var(--count));
    z-index: 0;
    left: 0;
    top: 0;
    transition: .3s ease;
    border-radius: 999px;
  }
}

@for $i from 1 through 20 {
  .radio-input label:nth-child(#{$i}):has(input:checked) ~ .selection {
    transform: translateX(calc(100% * #{$i - 1}));
  }
}