:root {
    --h: 100px;
}

.main-header {
    background-color: #ffffff00 !important;
    height: fit-content;
    line-height: var(--h);
    width: 80%;
    margin: 10px auto;
    display: flex;
    align-items: center;
}

.App-logo {
    height: var(--h);
    width: var(--h);
    margin: 0 auto;
    border-radius: 50%;
    transition: all .3s;
}

.App-logo:hover {
    transform: scale(1.05);
}

.title {
    color: #03A9F4 !important;
    font-family: 'Slidefu', monospace;
    position: absolute;
    height: var(--h);
    display: flex;
    align-items: center;
    font-size: 30px;
}

.search-input {
    width: 400px !important;
    line-height: 64px;
    margin-top: 17px;
    margin-left: 37px;
}

.main-menu {
    padding-left: 10% !important;
    padding-right: 10% !important;
    background-color: rgba(193, 205, 216, 0.175) !important;
}

.form {
    --width-of-input: 200px;
    --border-height: 1px;
    --border-before-color: rgba(221, 221, 221, 0.39);
    --border-after-color: #5891ff;
    --input-hovered-color: #4985e01f;
    position: relative;
    width: var(--width-of-input);
}

.input {
    font-size: 0.9rem;
    background-color: #c9e1ec50;
    width: 100%;
    box-sizing: border-box;
    padding-inline: 0.5em;
    padding-block: 0.7em;
    border: none;
    border-bottom: var(--border-height) solid var(--border-before-color);
}

.input-border {
    position: absolute;
    background: var(--border-after-color);
    width: 0%;
    height: 2px;
    bottom: 30%;
    left: 0;
    transition: 0.3s;
}

.input:hover {
    background: var(--input-hovered-color);
}

.input:focus {
    outline: none;
}

.input:focus~.input-border {
    width: 100%;
}

.btn {
    background: #5cbdfd;
    font-family: inherit;
    padding: 0.3em 1.3em;
    font-weight: 600;
    font-size: 12px;
    border: 2px solid #00000050;
    border-radius: 0.4em;
    box-shadow: 0.1em 0.1em;
    height: 30px;
    cursor: pointer;
    margin-left: 5px;
}

.btn:hover {
    transform: translate(-0.05em, -0.05em);
    box-shadow: 0.15em 0.15em;
}

.btn:active {
    transform: translate(0.05em, 0.05em);
    box-shadow: 0.05em 0.05em;
}